import React from 'react';
import './contact.css'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faPhone,faEnvelopeOpen} from '@fortawesome/free-solid-svg-icons';

function Contact(){
    return(
        <div className='contact_holder' id="contact">
            <div className='contact_card'>
                <h3>Let We discuss.. <span className='blinker'>_</span></h3>
                <div className='phone'>
                    <h3>I will help to ring him</h3>
                    <FontAwesomeIcon icon={faPhone} className="cnt_icn ph"/>
                    <h3>8220573831</h3>
                </div>
                <div className='email'>
                    <h3><a href='mailto:muhilanvenkat@gmail.com' className='mailer'>muhilanvenkat@gmail.com</a></h3>
                    <FontAwesomeIcon icon={faEnvelopeOpen} className="cnt_icn eml"/>
                    <h3>I'm always open to carry your Words</h3>
                </div>
            </div>
        </div>
    )
}

export default Contact