import React from 'react';
import './experience.css';
import Work from './pic3.svg';
import { gsap} from 'gsap';
import {ScrollTrigger} from 'gsap/ScrollTrigger';
gsap.registerPlugin(ScrollTrigger)

function Experience(){
    React.useEffect(()=>{
        let tl=gsap.timeline({
            scrollTrigger:{
                trigger:".workimage",
                endTrigger:".work1",
                start:"top 70%",
                end:"50% 50%",
                scrub:true
            }
        })
        tl.fromTo(".work_image",{
            opacity:0,
            y:-200
        },{
            opacity:1,
            y:0
        })
        .fromTo(".work1",{
            opacity:0,
            x:-200
        },{
            opacity:1,
            x:0
        })
        .fromTo(".internship1",{
            opacity:0,
        },{
            opacity:1,
        })
    })
    return(
        <div className='experience_holder' id="work">
            <div className='workimage'>
                <img src={Work} alt="work" className='work_image' title="work"/>
            </div>
            <div className='workdetails'>
                <div className='work1'>
                    <h5>Cognizant Technology Solutions</h5><br/>
                    <h3>Front End Web Developer (Jun'21-Oct'22)</h3><br/>
                    <ul>
                        <li>Creating Responsive Modification in the existing
website as per the Client request</li>
                        <li>Collaborated with senior developer during
development processes to confirm creative
proposals and design best practices.
</li>
                        <li>Followed SDLC best practices within Agile
environment to produce rapid iterations for clients</li>
                        <li>Researched emerging web technologies and trends
for possible incorporation into sites.
</li>
                        <li>Advocated for well-tested and documented, high
quality code.</li>
                        <li>Produced websites compatible with multiple
browsers</li>
                    </ul>
                </div>  
                <div className='internship1'>
                <h5>Fire Brands Lab</h5><br/>
                <h3>Web Developer -InternShip (Mar'21-May'21)</h3><br/>
                <ul>
                    <li>Work as a intern in Advertising and Marketing
company as a frontend web developer for the
duration of 3 months</li>
                    <li>Worked in various projects with seniors developers</li>
                </ul>   
                </div>  
            </div>
        </div>
    )
}

export default Experience