import React from 'react';
import './about.css';
import details from './about_data';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faHtml5,faCss3Alt,faJs,faFigma,faReact,faGitAlt} from "@fortawesome/free-brands-svg-icons";
import Cards from './card';
import {gsap} from 'gsap';
import {ScrollTrigger} from 'gsap/ScrollTrigger';
import {TextPlugin} from 'gsap/TextPlugin'
gsap.registerPlugin(ScrollTrigger,TextPlugin);


function About(){
    let Card=details.map((i)=>{
         return(
             <Cards key={i.id} image={i.img} role={i.role} className={`acard c${i.id}`}/> 
             );
    })
    React.useEffect(()=>{
        let tl=gsap.timeline({
            scrollTrigger:{
                trigger:".achieve",
                start: "top 70%",
                end:"top 10%",
                scrub:true
            }
        })
        tl.fromTo(".cardimages",{
            opacity:0,
        },{
            opacity:1,
            stagger:2,
        })
    },[])
    return(
        <div className='about_holder' id="about">
            <div className='about_left'>
                <div className='achieve'>
                    <h1>About</h1><br/>
                    <h3 className='ah1'>
                        Vice-President of Center For Entrepreneur
                    Development cell in our collage
                    </h3>
                    <h3 className='ah2'>
                    Vice-President of EDIT ( Eclet Developers of
                    Information Technology )Association of Information
                    Technology in our college(2019-2020)
                    </h3>
                </div>
                <div className='about_holder_left2'>
                    <h2>COMPETENCIES</h2>
                    <div className='card_holder'>
                        {Card}    
                    </div>
                </div>
            </div>
            <div className='about_right'>
                <div className='cube_holder'>
                    <div className='cubespinner'>
                        <div className='face1'>
                            <FontAwesomeIcon icon={faHtml5} color="#F06529"/>
                        </div>
                        <div className='face2'>
                            <FontAwesomeIcon icon={faCss3Alt} color="#28A4D9"/>
                        </div>
                        <div className='face3'>
                             <FontAwesomeIcon icon={faJs} color="#EFD81D"/>
                        </div>
                        <div className='face4'>
                             <FontAwesomeIcon icon={faFigma} color="#9c47fc"/>
                        </div>
                        <div className='face5'>
                             <FontAwesomeIcon icon={faReact} color="#5ED4F4"/>
                        </div>                        
                        <div className='face6'>
                            <FontAwesomeIcon icon={faGitAlt} color="#DD0031"/>
                        </div> 
                    </div>
                </div>
            </div>
        </div>
    )
}

export default About