import React, { useState } from 'react';
import './nav.css';
import PersonIcon from '@mui/icons-material/Person';
import WorkIcon from '@mui/icons-material/Work';
import InfoIcon from '@mui/icons-material/Info';
import PermContactCalendarIcon from '@mui/icons-material/PermContactCalendar';
import {gsap} from 'gsap';
import {ScrollTrigger} from 'gsap/ScrollTrigger';
gsap.registerPlugin(ScrollTrigger);

function NavBar(){
    const [active,setActive]=React.useState(1);
    return(
            <div className='navbar_container'>
                <ul>
                    <li onClick={()=>setActive(1)} className={active===1?"active":""}>
                        <a href="#home">
                            <PersonIcon style={{fontSize:'30px'}}/>
                        </a>
                    </li>
                    <li onClick={()=>setActive(2)} className={active===2?"active":""}>
                        <a href="#work">
                            <WorkIcon style={{fontSize:'30px'}}/>
                        </a>
                    </li>
                    <li onClick={()=>setActive(3)} className={active===3?"active":""}>
                        <a href="#about">
                            <InfoIcon style={{fontSize:'30px'}}/>
                        </a>
                    </li>
                    <li onClick={()=>setActive(4)} className={active===4?"active":""}>
                        <a href="#contact">
                            <PermContactCalendarIcon style={{fontSize:'30px'}}/>
                        </a>
                    </li>
                </ul>
            </div>
    )
}

export default NavBar


